export default {
	props:["nav"],
	data() {
		return {
			statusBarHeight: 0, 
			titleBarHeight: 0, 
			isCheck: true,
		}
	},
	created() {
		var  that = this;
		 	uni.getSystemInfo({
		 		success: function(res) {
		 			if (res.model.indexOf('iPhone') !== -1) {
						that.titleBarHeight = 44 + 'px';
		 			} else {
		 				that.titleBarHeight = 48  + 'px';
					}
						that.statusBarHeight = res.statusBarHeight  + 'px'
				},
					
		 	})
	 },
	methods: {
		 // 回到上一页
		back: function(){
		    uni.navigateBack({
				delta:1
		    })
		},
		checkTab(value) {
			this.isCheck = value; 
			this.$emit('changePage',this.isCheck)
		}
	}
}
